import React, { Component } from "react";

const partners = [
  "Daugyvenės kultūros istorijos muziejus - draustinis",
  "Šiaulių „Aušros“ muziejus",
  "Kurtuvėnų regioninis parkas",
  "Asociacija „Retromobile“",
  "UAB „Šeduvos malūnas“",
  "Šeduvos Šv. Kryžiaus Atradimo bažnyčia",
  "Šeduvos žydų memorialinis fondas",
  "Projektą finansuoja Lietuvos kultūros taryba"
];

const lktLogo = "./images/partners/B_W_LTK_Logotipas_Juodas.png";

const partnersIcons = [
  "./images/partners/B_W_Aušros muziejus logo.png",
  "./images/partners/B_W_Daugyvienės logo.png",
  "./images/partners/B_W_Kurtuvėnų parko logo.png",
  "./images/partners/B_W_Retromobilių logo.png",
  "./images/partners/B_W_Šeduvos malūno logo.png",
  "./images/partners/B_W_Šeduvos žydų memorialinis fondas logo.png"
];

export class Footer extends Component {
  render() {
    return (
      <div className="footer-wrapper">
      <div className="footer-container">
        <div className="column">
          <h1>Projektą finansuoja</h1>
          <div className="logo" style={{ backgroundImage: `url("${lktLogo}")` }}></div>
        </div>
        <div className="column partners-container">
          <h1>Partneriai</h1>
          <div className="partners-wrapper">
            {partnersIcons.map((partnerIcon, index) => {
              return (
                <div className="partner" key={index}>
                  <div className="logo" style={{ backgroundImage: `url("${partnerIcon}")` }}></div>
                </div>
              );
            })}
          </div>
        </div>
        </div>
      </div>
    );
  }
}

export default Footer;
