import React, { Component } from "react";
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { TextureLoader, MeshStandardMaterial } from "three";

import OrbitControls from "three-orbitcontrols";

class CubeRender extends Component {
  componentDidMount() {
    var scene = new THREE.Scene();
    var camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);

    var renderer = new THREE.WebGLRenderer();
    renderer.setSize(400, 400);
    this.mount.appendChild(renderer.domElement);

    var geometry = new THREE.BoxGeometry(2, 2, 1);
    var material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });

    var modelUrl = `./models/model_1.glb`;
    var textureUrl = `./textures/model_1.jpg`;

    const manager = new THREE.LoadingManager();
    manager.onStart = () => {
      //this.setState({ modelLoading: true });
      this.props.setModelLoaded(false);
    };

    manager.onLoad = () => {
      // this.setState({ modelLoading: false });
      this.props.setModelLoaded(true);
    };

    manager.onProgress = function () {};

    var loader = new GLTFLoader();

    loader.load(
      modelUrl,
      (gltf) => {
        var model = gltf.scene;
        // this.setModelTransforms();

        const textureLoader = new TextureLoader();
        let texture1 = textureLoader.load(textureUrl);

        texture1.wrapS = THREE.RepeatWrapping;
        texture1.flipY = false;

        const modelMaterial = new MeshStandardMaterial({
          roughness: 0.7,
          map: texture1
        });
        modelMaterial.side = THREE.DoubleSide;
        model.traverse(function (object) {
          if (object.isMesh) {
            object.material = modelMaterial;
          }
        });
        scene.add(model);
      },
      function (xhr) {
        // console.log((xhr.loaded / xhr.total) * 100 + "% loaded");
      },
      function (error) {
        //console.log("An error happened");
      }
    );

    console.log(scene);

    var lights = [];
    lights[0] = new THREE.PointLight("red", 50, 0);
    lights[1] = new THREE.AmbientLight("white", 0, 0);
    lights[0].position.set(0, 0, 1);
    scene.add(lights[0]);
    scene.add(lights[1]);

    material = new MeshStandardMaterial({
      roughness: 0.7
    });
    var cube = new THREE.Mesh(geometry, material);
    scene.add(cube);

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.rotateSpeed = 1;

    camera.position.z = 3;
    renderer.render(scene, camera);
  }
  render() {
    return <div className="cube" ref={(ref) => (this.mount = ref)} />;
  }
}

export default CubeRender;
