import React, { Component } from "react";

export class WebsiteInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      readMoreLtClicked: false,
      readMoreEnClicked: false
    };
    this.textBoxLtRef = React.createRef();
    this.textBoxEnRef = React.createRef();
    this.textPart1Ref = React.createRef();
    this.textPart2Ref = React.createRef();
    this.textPart3Ref = React.createRef();
    this.textPart4Ref = React.createRef();
  }

  readMoreLtClicked = () => {
    const initialState = this.state.readMoreLtClicked;
    this.setState({ readMoreLtClicked: !initialState });
    if (initialState) {
      this.textBoxLtRef.current.style.height = `${this.textPart1Ref.current.clientHeight}px`;
    } else {
      this.textBoxLtRef.current.style.height = `${this.textPart1Ref.current.clientHeight + this.textPart2Ref.current.clientHeight}px`;
    }
  };

  readMoreEnClicked = () => {
    const initialState = this.state.readMoreEnClicked;
    this.setState({ readMoreEnClicked: !initialState });
    if (initialState) {
      this.textBoxEnRef.current.style.height = `${this.textPart3Ref.current.clientHeight}px`;
    } else {
      this.textBoxEnRef.current.style.height = `${this.textPart3Ref.current.clientHeight + this.textPart4Ref.current.clientHeight}px`;
    }
  };

  componentDidMount() {
    this.textBoxLtRef.current.style.height = `${this.textPart1Ref.current.clientHeight}px`;
    this.textBoxEnRef.current.style.height = `${this.textPart3Ref.current.clientHeight}px`;
  }
  render() {
    // const textBoxLtClass = this.state.readMoreLtClicked ? "text-box-lt-open" : "";
    // const textBoxEnClass = this.state.readMoreEnClicked ? "text-box-en-open" : "";

    return (
      <div className="website-info">
        <div className="website-info-top">
          <div className="text-box">
            <h1>Virtualus Šeduvos turas:</h1>
            <h1>„Kelionė laiku“</h1>
          </div>
          <a className="logo-image" href="https://www.seduvoskultura.lt/" target="_blank" rel="noreferrer">
            <div className="logo-text" style={{ display: "none" }}>
              text
            </div>
          </a>
        </div>
        <div className="text-boxes-container">
          <div className={`text-box text-box-lt`}>
            <h1>Apie projektą:</h1>
            <div className="text" ref={this.textBoxLtRef}>
              <div className="text-part1 text-part" ref={this.textPart1Ref}>
                Projektas Šeduvos virtualus turas „Kelionė laiku“ kviečia keliauti trimis skirtingais maršrutais: „Šeduvos istorija“,
                „Šeduvos legendos“ ir „Šeduva dabar“. Pirmame maršrute „Šeduvos istorija“ susipažinsite su miesto istorija, apžiūrėsite
                Daugyvenės muziejaus Šeduvos kraštotyros skyriaus rinkinių 3D parodą, išgirsite Šeduvos Babos – Emilijos Brajinskienės
                pasakojimą apie miesto kilmę.
              </div>
              <div className="text-part2 text-part" ref={this.textPart2Ref}>
                Maršrute „Legendos“ pamatysite 7 žymiausias ir įdomiausias Šeduvos krašto legendas, kurias vizualizuos bei įgarsins
                profesionalūs ir žinomi Valstybinio Šiaulių dramos teatro aktoriai: Sigitas Jakubauskas, Monika Šaltytė-Dovydavičienė,
                Nomeda Bėčiutė, Severinas Norgaila bei „Šeduvos bernai“. Trečiajame maršrute „Šeduva dabar“ keliausite po Šeduvos lankytinas
                vietas bei kultūros paveldo objektus 360° kampu.
              </div>
            </div>
            <div className="btn btn-read-more" onClick={this.readMoreLtClicked}>
              {this.state.readMoreLtClicked ? "Skaityti mažiau" : "Skaityti daugiau"}
            </div>
          </div>
          <div className={`text-box text-box-en`}>
            <h1>About project:</h1>
            <div className="text" ref={this.textBoxEnRef}>
              <div className="text-part3 text-part" ref={this.textPart3Ref}>
                The project Šeduva virtual Tour "Journey in Time" invites you to travel on three different routes: "History of Šeduva",
                "Legends of Šeduva" and "Šeduva nowadays". On the first route "History of Šeduva" you will get acquainted with the history
                of the town, see a 3D exhibition of Šeduva Local Ethnographic Department Daugyvene Museum‘s collections , listen to the
                story of Šeduva‘s Baba - Emilija Brajinskienė about the origin of the city.
              </div>
              <div className="text-part4 text-part" ref={this.textPart4Ref}>
                On the route "Legends" you will see 7 most famous and interesting legends of Šeduva region, which will be visualized and
                dubbed by professional and well-known actors of Šiauliai State Drama Theater: Sigitas Jakubauskas, Monika
                Šaltytė-Dovydavičienė, Nomeda Bėčiutė, Severinas Norgaila and "Šeduva‘s Guys"folk group. On the third route "Šeduva
                nowadays" you will travel around the sights and cultural heritage objects of Šeduva at a 360 ° angle.
              </div>
            </div>
            <div className="btn btn-read-more" onClick={this.readMoreEnClicked}>
              {this.state.readMoreEnClicked ? "Read less" : "Read more"}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default WebsiteInfo;
